.navigation {
    text-align: center;
    
    &__link{
        @extend %text-link;
        box-shadow: 5px 5px 5px black;
        border-radius: 10px;
        padding: 5px;
        margin: 10px 10px;
        transition: 0.5s;

        &:hover {
            background-color: aqua;
        }
    }
}